import React, { Component} from 'react';
import { withRouteData, Link } from 'react-static';
import Box from 'grommet/components/Box';
import Anchor from 'grommet/components/Anchor';
import Hero from 'grommet/components/Hero';
import Headline from 'grommet/components/Headline';
import Card from 'grommet/components/Card';
import Image from 'grommet/components/Image';
import Label from 'grommet/components/Label';
import Heading from 'grommet/components/Heading';
import Status from 'grommet/components/icons/Status';
import Form from 'grommet/components/Form';
import Button from 'grommet/components/Button';
import Header from 'grommet/components/Header';
import Footer from 'grommet/components/Footer';
import Columns from 'grommet/components/Columns';
import Paragraph from 'grommet/components/Paragraph';
import FormFields from 'grommet/components/FormFields';
import FormField from 'grommet/components/FormField';
import TextInput from 'grommet/components/TextInput';
import Toast from 'grommet/components/Toast';
import Responsive from 'grommet/utils/Responsive';
import Carousel from 'grommet/components/Carousel';
import Tabs from 'grommet/components/Tabs';
import Tab from 'grommet/components/Tab';
import Menu from 'grommet/components/Menu';
import Markdown from 'grommet/components/Markdown';
import CheckmarkIcon from 'grommet/components/icons/base/Checkmark';
import PlatformChromeIcon from 'grommet/components/icons/base/PlatformChrome';
import PlatformSafariIcon from 'grommet/components/icons/base/PlatformSafari';
import PlatformFirefoxIcon from 'grommet/components/icons/base/PlatformFirefox';
import PlatformEdgeIcon from 'grommet/components/icons/base/PlatformEdge';
import PlatformInternetExplorerIcon from 'grommet/components/icons/base/PlatformInternetExplorer';
import LikeIcon from 'grommet/components/icons/base/Like';

//jscs:disable maximumLineLength
// eslint-disable-next-line no-useless-escape
const reEmail = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
//jscs:enable maximumLineLength
//
class HomePage extends Component {
  constructor(props) {
    super(props);

    this.handleDemo = this.handleDemo.bind(this);
    this.handleMoreInfo = this.handleMoreInfo.bind(this);
    this.handleGotoProduct = this.handleGotoProduct.bind(this);
    this.handleGotoServices = this.handleGotoServices.bind(this);
    this.onResponsive = this.onResponsive.bind(this);

    this.state = {
      sent: false,
      sent2: false
    };
  }

  componentDidMount() {
    this.responsive = Responsive.start(this.onResponsive);
    this.jumpToHash();
  }

  componentDidUpdate() {
    this.jumpToHash();
  }

  componentWillUnmount() {
    this.responsive.stop();
  }

  onResponsive(small) {
    this.setState({small});
  }

  jumpToHash() {
    const scrollToElement = require('scroll-to-element');
    const hash = window.location.hash;
    if (hash) {
      scrollToElement(hash, {offset: -120});
    }
  }

  handleGotoProduct() {
  }

  handleGotoServices() {
  }

  requestDemo(email, error2) {
    const xmlhttp = new XMLHttpRequest();
    const self = this;
    xmlhttp.onreadystatechange = function() {
      if (xmlhttp.readyState == XMLHttpRequest.DONE) { // XMLHttpRequest.DONE == 4
        if (xmlhttp.status == 200) {
          if (error2) {
            self.setState({error: null, sent2: true});
          } else {
            self.setState({error2: null, sent: true});
          }
        } else {
          const error = 'An error occurred sending. Please try again later.';
          if (error2) {
            self.setState({error2: error});
          } else {
            self.setState({error});
          }
        }
      }
    };

    xmlhttp.open('POST', 'https://api.1-goal.com/methods//api/demo/request', true);
    xmlhttp.setRequestHeader("Content-Type", "application/json");
    xmlhttp.send(JSON.stringify([email]));
  }

  handleDemo(e) {
    const email = document.getElementById('email').value;
    if (reEmail.test(email)) {
      this.requestDemo(email, false);
    } else {
      this.setState({error: 'Please enter valid email'});
      e.preventDefault();
    }
    return false;
  }

  handleMoreInfo(e) {
    const email = document.getElementById('email2').value;
    if (reEmail.test(email)) {
      this.requestDemo(email, true);
    } else {
      this.setState({error2: 'Please enter valid email'});
      e.preventDefault();
    }
    return false;
  }

  renderListItem(text, index) {
    return (
      <Box alignSelf="start" key={index} direction="row" align="start" responsive={false} pad="small" margin="small">
        <Box pad={{horizontal: 'small'}} style={{marginTop: '4px'}}>
          <CheckmarkIcon size="small" />
        </Box>
        <Heading margin="none" tag="h3">{text}</Heading>
      </Box>);
  }

  render() {
    const {allBenefits, allProductFeatures} = this.props;
    return (
    <Box>
      {this.state.small &&
        <Header colorIndex="brand" justify="center">
          <Box size={{width: {max: 'xxlarge'}}} direction="row" responsive={false} justify="start" align="center"
            pad={{horizontal: 'medium'}} flex="grow">
            <a href="http://www.radioactive.io">
              <Image src="/onegoal-logo-white-2.svg" size="small" />
            </a>
            <Box pad="small" />
          </Box>
        </Header>
      }
      <Hero id="home-page" style={{alignItems: 'flex-start', maxWidth: '100%'}} size="medium"
        backgroundColorIndex="dark" background={<Image src="/onegoal-splash.jpg" fit="cover" full={true} />}>
        {!this.state.small &&
        <Header justify="center">
          <Box direction="row" style={{width: '700px', maxWidth: '100%'}} responsive={false} justify="start"
            align="center">
            <Box>
              <a href="http://www.1-goal.com">
                <Image src="/onegoal-logo-white-2.svg" size="small" />
              </a>
            </Box>
            <Box pad="small" />
            <Menu inline={true} direction="row" flex="grow" justify="end" align="end">
              <Link to="#product">Product</Link>
              <Link to="#services">Services</Link>
              <a href="http://www.radioactive.io">About</a>
            </Menu>
          </Box>
        </Header>
        }
        <Box direction="column" size={{width: {max: 'xxlarge'}}} alignSelf="center" justify="center" align="center"
          pad="large">
          <Headline align="center" strong={true} size="medium">Turn Strategy into Action.</Headline>
          <Heading align="center" tag="h2">OneGoal is a strategy execution methodology and software solution.</Heading>
          <Box>
            {this.state.sent ? (
              <Box align="center" justify="center" direction="row">
                <Status size="large" value="ok" />&nbsp;&nbsp;
                <Heading margin="none" tag="h3">Request Sent</Heading>
              </Box>
            ) : (
            <Form pad="medium" plain={true} >
              <Header justify="center" align="center">
                <Heading align="center" tag="h3">Contact Sales for more information.</Heading>
              </Header>
              <FormFields>
                <fieldset>
                  <FormField error={this.state.error} label="Email" htmlFor="email">
                    <TextInput id="email" placeHolder="your_name@your_company.com" />
                  </FormField>
                </fieldset>
              </FormFields>
              <Footer pad={{'vertical': 'medium'}}>
                <Button fill={true} label="Request Demo" onClick={this.handleDemo} />
              </Footer>
            </Form>
            )}
          </Box>
        </Box>
      </Hero>
      <Box id="benefits" colorIndex="light-2" align="start" direction="row">
        <Box margin="large" size="full" align="center">
          <Box align="center" size={{'width': 'xxlarge'}} pad={{horizontal: 'large'}}>
            <Heading tag="h1" align="center" strong={true}>Not getting the results you want?</Heading>
          </Box>
          <Box pad="small">
            <Paragraph align="center" margin="none" size="large" style={{maxWidth: '100%'}}>
              You have a great strategy, but like most organizations, execution is a challenge.
              Too many distractions, changing priorities, ineffective communication, not aligned for success.
              <strong> The good news? OneGoal can help you.</strong>
            </Paragraph>
          </Box>
          <Box direction="row">
            <Box colorIndex="light-1" style={this.state.small ? {} : {width: '66%'}} justify="center" margin="large">
              <Columns responsive={true} justify="center" maxCount={2} masonry={true}>
                {allBenefits.map((benefit, index) =>
                  <Box key={index} direction="row" align="start" responsive={false} colorIndex="light-1" pad="small"
                  size={this.state.small ? 'auto' : 'medium'} margin="small">
                    <Box pad={{horizontal: 'small'}} style={{marginTop: '4px'}}>
                      <Status value="ok" />
                    </Box>
                    <Heading margin="none" tag="h3">{benefit.description}</Heading>
                  </Box>
                )}
              </Columns>
            </Box>
            <Box margin="large" align="center">
              <img src="61.png" alt="61 %" />
              <Box pad="medium">
              <Paragraph margin="none" align="center" size="medium">61% of strategies dramatically underperform â€”
                not because of faulty ideas or poor planning,
                but as a result of fundamentally poor execution.</Paragraph>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
      <Box id="product" colorIndex="light-1" margin="large" align="center">
        <Box size={{'width': 'xxlarge'}} pad={{horizontal: 'large'}}>
          <Heading tag="h1" align="center" strong={true}>The Solution</Heading>
        </Box>
        <Tabs responsive={false}>
         {allProductFeatures.map((productFeature, index) =>
           <Tab key={index} title={productFeature.title}>
             <Box direction="row">
               <Box basis="1/2" margin="large">
                 <Heading tag="h3" strong={true}>{productFeature.shortDescription}</Heading>
                 <Markdown components={{"p": {"props": {"size": "large"}}}} content={productFeature.description} />
               </Box>
               <Box basis="1/2">
                 <img alt={productFeature.title} src={`https://media.graphcms.com/${productFeature.image.handle}`} />
               </Box>
             </Box>
           </Tab>
         )}
        </Tabs>
        <Box pad={{between: 'large'}} justify="center" responsive={true} direction="row">
          <Box style={this.state.small ? {} : {width: '25%'}} pad="medium" separator="all">
            <Paragraph size="large" margin="none"><strong>"<LikeIcon size="small" /> Love this app."</strong>
            </Paragraph>
            <br/>
            <Paragraph>- Will Brennan</Paragraph>
          </Box>
          <Box style={this.state.small ? {} : {width: '25%'}} pad="medium" separator="all">
            <Paragraph size="large" margin="none"><strong>"I like the simplicity of the product..."</strong></Paragraph>
            <Paragraph>- Nada Nazari</Paragraph>
          </Box>
          <Box style={this.state.small ? {} : {width: '25%'}} pad="medium" separator="all">
            <Paragraph size="large" margin="none"><strong>"...very convenient and I am a big fan..."</strong>
            </Paragraph>
            <Paragraph>- Ben Stanley</Paragraph>
          </Box>
        </Box>
      </Box>
      <Box id="services" align="center" pad="large" colorIndex="neutral-1">
        <Box size={{'width': 'xxlarge'}} pad={{horizontal: 'large'}}>
          <Heading tag="h1" align="center" strong={true}>Let us help you</Heading>
        </Box>
        <Box direction="row">
          <Box basis="1/2" align="center" margin={{top: 'medium'}}>
            <Heading tag="h2" align="center" strong={true}>Strategy Facilitation</Heading>
            <Box pad="small">
              <Paragraph style={{maxWidth: '100%'}} size="large">
                The OneGoal team helps organizations develop sound plans by implementing the OneGoal methodology:
              </Paragraph>
            </Box>
            {[`An executive leadership 2-day strategy session to develop annual goals that support company
              breakthrough objectives.`,
              'A manager 1-day training session to establish aligning goals and activities.',
              `"Train the trainer" remote sessions designed to equip 1 or more internal coaches to achieve
              sustaining success over time.`,
              'A launch day review to ensure team compliance with the OneGoal methodology.'].map((text, index) =>
              this.renderListItem(text, index))}
          </Box>
          <Box basis="1/2" align="center" margin={{top: 'medium'}}>
            <Heading tag="h2" align="center" strong={true}>Customer Success</Heading>
            <Box pad="small">
              <Paragraph style={{maxWidth: '100%'}} size="large">
                The OneGoal team is your partner in implementation of the product by providing:
              </Paragraph>
            </Box>
            {['Established best practices based on your goals, organization structure, and budget.',
              'Data review for your team data, rewards, and branding.',
              'Two 30-minute live training sessions customized for your deployment.',
              'Promotional material to build excitement during your launch.',
              'Ongoing monitoring and data validation as you progress towards goal achievement'
            ].map((text, index) =>
              this.renderListItem(text, index))}
          </Box>
        </Box>
        <Box>
          {this.state.sent2 ? (
            <Box align="center" justify="center" direction="row">
              <Status size="large" value="ok" />&nbsp;&nbsp;
              <Heading margin="none" tag="h3">Request Sent</Heading>
            </Box>
          ) : (
          <Form pad="medium" plain={this.state.small} >
            <Header justify="center" align="center">
              <Heading align="center" tag="h3">Contact Sales for more information.</Heading>
            </Header>
            <FormFields>
              <fieldset>
                <FormField error={this.state.error2} label="Email" htmlFor="email2">
                  <TextInput id="email2" placeHolder="your_name@your_company.com" />
                </FormField>
              </fieldset>
            </FormFields>
            <Footer pad={{'vertical': 'medium'}}>
              <Button fill={true} label="Get Started" onClick={this.handleMoreInfo} />
            </Footer>
          </Form>
          )}
        </Box>
      </Box>
      <Box id="more" align="center" pad="large" colorIndex="light-1">
        <Box pad={{horizontal: 'large'}}>
          <Heading align="center" tag="h2" strong={true} margin="none">OneGoal is available on the web,
            iOS, and Android.</Heading>
        </Box>
        <Box direction="row" pad={{between: 'medium'}} margin={{top: 'medium'}}>
          <Box basis="1/2" pad={{horizontal: 'medium'}} align="center">
            <Heading tag="h3" align="center" strong={true}>OneGoal for iOS and Android Devices</Heading>
            <Box>
              <Box align="center" justify="center" direction="row">
                <a href="https://itunes.apple.com/us/app/1-goal/id1249692380?mt=8">
                  <img src="/app-store-badge.svg" style={{padding: '10px'}} width="148px" />
                </a>
                <a href="https://play.google.com/store/apps/details?id=io.onegoal.app">
                  <img src="/google-play-badge.png" width="148px" />
                </a>
              </Box>
              <Box pad="medium" align="center" justify="center">
                <img src="/apple-touch-icon.png" width="72px" className="appIcon" />
                <Label size="small">OneGoal</Label>
              </Box>
            </Box>
          </Box>
          <Box basis="1/2" pad={{horizontal: 'medium'}} align="center">
            <Heading tag="h3" align="center" strong={true}>OneGoal in your web browser</Heading>
            <Paragraph align="center">OneGoal supports the latest version of the following browsers:</Paragraph>
            <Box direction="row" responsive={false} justify="center" pad={{between: 'small'}}>
              <Box align="center" size="xsmall">
              <PlatformChromeIcon size="medium" />
              <Label align="center">Chrome</Label>
              </Box>
              <Box align="center" size="xsmall">
              <PlatformSafariIcon size="medium" />
              <Label align="center">Safari</Label>
              </Box>
              <Box align="center" size="xsmall">
              <PlatformFirefoxIcon size="medium" />
              <Label align="center">Firefox</Label>
              </Box>
              <Box align="center" size="xsmall">
              <PlatformEdgeIcon size="medium" />
              <Label align="center">Microsoft Edge</Label>
              </Box>
              <Box align="center" size="xsmall">
              <PlatformInternetExplorerIcon size="medium" />
              <Label align="center">Internet Explorer</Label>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
      {this.state.sent &&
        <Toast status="ok">Your Request has been sent. A sales representative will follow up with you soon.</Toast>
      }
      {this.state.sent2 &&
        <Toast status="ok">Your Request has been sent. A sales representative will follow up with you soon.</Toast>
      }
      {this.state.errorSending &&
        <Toast status="ok">An error occurred sending your request. Please try again later.</Toast>
      }
    </Box>);
  }
}

export default withRouteData(({allPosts, allBenefits, allProductFeatures}) => (
  <HomePage allPosts={allPosts} allBenefits={allBenefits} allProductFeatures={allProductFeatures} />
));



