import React, { Fragment } from 'react';
import { Router } from 'react-static';
import Routes from 'react-static-routes';
import App from 'grommet/components/App';
import Footer from 'grommet/components/Footer';
import Box from 'grommet/components/Box';
import Heading from 'grommet/components/Heading';
import Anchor from 'grommet/components/Anchor';
import Paragraph from 'grommet/components/Paragraph';
import SocialTwitterIcon from 'grommet/components/icons/base/SocialTwitter';
import SocialFacebookIcon from 'grommet/components/icons/base/SocialFacebook';
import SocialMailIcon from 'grommet/components/icons/base/SocialMail';

import './main.scss';

function  renderEmailAnchor() {
  const coded = 'V5QQM@wBPJMBS2JT5.JM';
  const key = 'LuQ6N5MBbckH9TfXm02zrCidOFhy3SUWKD1qj4GEw7JPlRVeoaxsAgIvn8YZtp';
  const shift = coded.length;
  let link = '';
  for (let i = 0; i < coded.length; i++) {
    if (key.indexOf(coded.charAt(i)) == -1) {
      const ltr = coded.charAt(i);
      link += (ltr);
    } else {
      const ltr = (key.indexOf(coded.charAt(i)) - shift + key.length) % key.length;
      link += (key.charAt(ltr));
    }
  }
  return <Anchor href={'mailto:' + link} label="email" icon={<SocialMailIcon />} target="_self" />;
}

export default () => (
  <Router>
    <Fragment>
      <main>
        <App centered={false}>
          <Routes />
          <Footer full="horizontal">
            <Box id="contact" colorIndex="brand" pad="medium" align="center" full="horizontal">
              <Box className="footer-cards-container" pad={{vertical: 'medium'}} size={{width: 'xxlarge'}}
                direction="row" flex="grow">
                <Box pad="large" basis="1/2" separator="right">
                  <Heading tag="h3">Contact Us</Heading>
                  <Box pad={{vertical: 'small'}}>
                    {renderEmailAnchor()}
                  </Box>
                  <Box pad={{vertical: 'small'}}>
                    <Anchor href="https://twitter.com/radioactive_io" target="_blank" label="twitter"
                      icon={<SocialTwitterIcon />} />
                  </Box>
                  <Box pad={{vertical: 'small'}}>
                    <Anchor href="http://facebook.com/radioactive.io" target="_blank" label="facebook"
                      icon={<SocialFacebookIcon />} />
                  </Box>
                </Box>
                <Box pad="large" basis="1/2">
                  <Heading tag="h3">Links</Heading>
                  <Anchor href="https://radioactive.zendesk.com/hc">Help Center</Anchor>
                  <Anchor href="http://www.radioactive.io/privacy">Privacy Policy</Anchor>
                  <Anchor href="http://www.radioactive.io/terms">Terms of Use</Anchor>
                  <Paragraph>Copyright Â© 2017 Radioactive.io, Inc. All Rights Reserved.</Paragraph>
                </Box>
              </Box>
            </Box>
          </Footer>
        </App>
      </main>
    </Fragment>
  </Router>
);



